@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App{text-align:center}.App-logo{height:40vmin;pointer-events:none}@media (prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:white}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}html,body{font-family:"Montserrat", sans-serif;line-height:1.6;scroll-behavior:smooth}

.nav{display:flex;position:fixed;width:100vw;flex-direction:row;background:#262626;font-size:18px;z-index:1000;height:4em;align-items:center}.nav a{margin-left:2em;text-decoration:none;color:#fff;font-weight:600;letter-spacing:3px}.nav .yellow{color:#fdd835}.nav-links{display:none}@media (min-width: 768px){.nav-links{display:flex;align-items:center;position:absolute;right:0.5em}}.nav-links a{text-decoration:none;line-height:0;color:#fff;display:flex;font-weight:500}@media (min-width: 768px){.nav-links a{margin:10px 15px;line-height:1}}.nav-links a:hover{transition:transform 0.5s ease}.nav-hamburger{position:absolute;right:1em;display:block}@media (min-width: 768px){.nav-hamburger{display:none}}

.menulist{width:15em}.menulist ul{padding-left:0}.menulist-item{width:100%}.menulist li{padding:15px;margin-left:20px;list-style:none}.menulist a{text-decoration:none;color:#262626;font-weight:600;font-size:18px}

.hamburgermenu{display:flex;height:100vh;flex-direction:column;justify-content:space-between}.hamburgermenu-title{display:flex;justify-content:center;align-items:center;background:#fdd835;padding:0.25em}.hamburgermenu-title a{text-decoration:none;color:#262626}.hamburgermenu-title p{font-size:18px;font-weight:600;letter-spacing:2px}.hamburgermenu-socials{display:flex;width:auto;justify-content:center;align-items:center;padding-bottom:2em}

.socials{display:flex;flex-direction:row}.socials-item{width:auto;font-weight:600;text-decoration:none;color:#fdd835;margin:10px}.socials-item div{padding:10px 20px;border:2px solid #fdd835;border-radius:50px}.socials-item:hover{color:#262626;transition:all 0.2s ease-out;-webkit-transition:all 0.2s ease-out;-moz-transition:all 0.2s ease-out}.socials-item:hover div:hover{background:#fdd835;transition:all 0.2s ease-out;-webkit-transition:all 0.2s ease-out;-moz-transition:all 0.2s ease-out}

.landing{display:flex;width:100vw;min-height:100vh;background:#17bebb;justify-content:center;color:#262626}.landing-container{margin-left:15px;max-width:800px;display:flex;flex-direction:column;justify-content:center;align-items:center;width:100%;height:90vh}

.content-block{display:flex;flex-direction:column;align-items:center;text-align:left;margin-left:0.75em;margin-right:0.75em;justify-content:center}.content-block-title{font-size:2em;text-align:left}.content-block-title h2{margin-bottom:0em}.content-block-subtitle{font-size:1.5em}@media (min-width: 768px){.content-block-content{align-items:flex-start;max-width:75vw}}

.about{background:#e2eef3;width:100vw;min-height:100vh;padding-top:2em;padding-bottom:2em;display:flex;flex-direction:column;justify-content:flex-start}

.graph-box{height:30em}.graph{display:flex;flex-direction:row;justify-content:center;overflow:hidden}.graph-bar{position:absolute;display:flex;justify-content:center;align-items:center;left:50;height:2em;background:#fff;-webkit-animation-name:growingBar;animation-name:growingBar;-webkit-animation-duration:0.5s;animation-duration:0.5s;-webkit-animation-fill-mode:forwards;animation-fill-mode:forwards;-webkit-animation-play-state:paused;animation-play-state:paused}.graph-label{display:flex;justify-content:center;align-items:center;position:absolute;padding-top:0.25em;color:#fff;font-weight:600}.graph-bar:hover{-webkit-animation-play-state:running;animation-play-state:running}.width-1{width:6em}@media (min-width: 768px){.width-1{width:8em}}.width-2{width:8em}@media (min-width: 768px){.width-2{width:12em}}.width-3{width:10em}@media (min-width: 768px){.width-3{width:16em}}.width-4{width:12em}@media (min-width: 768px){.width-4{width:20em}}.width-5{width:14em}@media (min-width: 768px){.width-5{width:24em}}.color-shade-0{background:#262626}.color-shade-1{background:#234948}.color-shade-2{background:#215857}.color-shade-3{background:#1f6a69}.color-shade-4{background:#1e7876}.color-shade-5{background:#1d8381}.color-shade-6{background:#1c8987}.color-shade-7{background:#1b918e}.color-shade-8{background:#1a9a97}.color-shade-9{background:#19a8a5}.color-shade-10{background:#17bebb}.level-1{margin-top:2.5em}.level-2{margin-top:5em}.level-3{margin-top:7.5em}.level-4{margin-top:10em}.level-5{margin-top:12.5em}.level-6{margin-top:15em}.level-7{margin-top:17.5em}.level-8{margin-top:20em}.level-9{margin-top:22.5em}.level-10{margin-top:25em}@-webkit-keyframes growingBar{0%{transform:scale(1, 1)}100%{transform:scale(1.3, 1)}}@keyframes growingBar{0%{transform:scale(1, 1)}100%{transform:scale(1.3, 1)}}

.work{background:#f1f6f9;width:100vw;min-height:100vh;padding-top:2em;padding-bottom:2em;display:flex;flex-direction:column;align-items:center}

.imagegroup{width:100vw;display:flex;flex-direction:column;flex-wrap:wrap;justify-content:center;margin-top:5em;margin-bottom:2em}@media (min-width: 768px){.imagegroup{flex-direction:row;width:95vw}}

.hoverableimage{display:flex;justify-content:center;margin-bottom:1em;margin-left:0.5em;margin-right:0.5em}@media (min-width: 768px){.hoverableimage{margin-left:0.5em}}.container{position:relative;width:90%}@media (min-width: 768px){.container{width:360px}}.image{display:block;width:100%;height:auto}.overlay{position:absolute;top:0;bottom:0;left:0;right:0;height:100%;width:100%;opacity:0;transition:0.5s ease;background-color:#262626}.container:hover .overlay{opacity:1}.text{color:white;font-size:16px;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);text-align:center}@media (min-width: 768px){.text{font-size:18px}}

.contact{display:flex;min-width:100vw;min-height:100vh;background:#f1f6f9;padding-bottom:4em;justify-content:center;color:#262626}.contact-container{margin-top:6em}.contact-container h1{font-size:3em}.contact-container-cta{box-shadow:0px 2px 52px -15px rgba(0,0,0,0.75);margin-top:4em;margin-left:0.5em;margin-right:0.5em;padding:1em 1em 2em 1em;border-radius:5px;background:#17bebb;display:flex;flex-direction:column;justify-content:center;align-items:center}@media (min-width: 768px){.contact-container-cta{font-size:18px;width:90vw}}

.projects{background:#e2eef3;width:100vw;min-height:100vh;padding-top:2em;padding-bottom:2em;display:flex;flex-direction:column;align-items:center}

.footer{width:100vw;display:flex;flex-direction:column;justify-content:center;align-items:center;background:#262626;opacity:0.9;color:#fff;padding-top:2em;padding-bottom:2em}

